import {cancelOrderApi} from "@/api/orders";

export default {
  methods: {
    // 如果需要弹出登录确认框，说明就是没登录，没登录是干不了下单操作的
    // return true 就是弹出了登录确认框，return false 就是没弹出登录确认框
    cancelOrderConfirm() {
      return this.$dialog.confirm({
        title: '友情提示',
        message: '确定要取消订单吗',
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      })
        .then(() => true)  // 点击确定返回 true
        .catch(() => false)  // 点击取消返回 false
    }
  }
}
