<script>
import OrderListItem from "@/components/OrderListItem.vue";
import {getOrderListApi} from "@/api/orders";

export default {
  name: "Orders",
  components: {
    OrderListItem
  },
  data() {
    return {
      active: this.$route.query.dataType || 'all', // 当前选中的tab栏
      page: 1, // 第几页的数据（15条一页）
      list: []
    }
  },
  methods: {
    // 根据不同的active获取订单列表
    async getOrderList() {
      const res = await getOrderListApi(this.active, this.page)
      const list = res.data.list.data

      // 计算每个订单的商品总数，一个订单可以有不同的多个商品
      list.forEach((item) => {
        item.total_num = item.goods.reduce((sum, good) => {
          return sum + good.total_num
        }, 0)
      })

      this.list = list
    }
  },
  watch: {
    active: {
      immediate: true,
      handler() {
        this.getOrderList()
      }
    }
  }
}
</script>

<template>
  <div class="orders">
    <header>
      <van-nav-bar title="我的订单" left-arrow @click-left="$router.go(-1)"/>

      <van-tabs v-model="active" style="">
        <van-tab name="all" title="全部"></van-tab>
        <van-tab name="payment" title="待支付"></van-tab>
        <van-tab name="delivery" title="待发货"></van-tab>
        <van-tab name="received" title="待收货"></van-tab>
        <van-tab name="comment" title="待评价"></van-tab>
      </van-tabs>
    </header>

    <div v-if="list.length > 0" class="content">
      <OrderListItem
          v-for="item in list"
          :key="item.order_id"
          :item="item"
          @order-updated="getOrderList"
          @click.native="$router.push(`/orderDetail?orderId=${item.order_id}`)"
      >
        <!--@click.native 相当于是在组件根元素使用原生的 click 事件，而不是使用自定义的 click 事件-->
      </OrderListItem>

      <!--如果有订单列表，那就可以显示底部，如果没有订单列表，那就不能显示底部-->
      <footer>
        亲，没有更多了
      </footer>
    </div>

    <div v-else class="empty" style="height: calc(100vh - 90px);">
      <img src="@/assets/empty.png" alt="">
      <div class="tips">
        亲，暂无订单记录
      </div>
    </div>


  </div>
</template>

<style scoped lang="less">

.orders {
  background-color: #fafafa;
}

header {
  position: sticky;
  top: 0;
  width: 100%;

  .van-tabs {
    border-bottom: 1px solid #ebedf0;
  }
}

footer {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: gray;
}

.empty {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  img {
    width: 140px;
    height: 92px;
  }

  .tips {
    margin: 30px 0;
    color: #666;
  }
}
</style>
